import {mapState} from 'vuex';
export default {
  computed: {
    ...mapState({
      OrderGoodsInfos: (data) => {
        let OrderGoodsInfos = [];
        data.CartInfo.forEach((val) => {
          if (val.isChecked) {
            OrderGoodsInfos.push(val);
          }
        });
        return OrderGoodsInfos;
      },
      ParcelNum: (data) => {
        let ParcelNum = 0;
        data.CartInfo.forEach((val) => {
          if (val.isChecked) {
            ParcelNum += val.count;
          }
        });
        return ParcelNum;
      }
    })
  },
  activated() {
    this.$nextTick(() => {
      this.$store.commit('isLoading', false);
    });
  }
};



