import VueCoreImageUpload from 'vue-core-image-upload';
import WeVue from 'we-vue';
export default {
  data() {
    return {
      valueContent: this.valueContent,
      src: []
    };
  },
  activated () {
    this.$nextTick(() => {
      this.$store.commit('isLoading', false);
    });
  },
  components: {
    'vue-core-image-upload': VueCoreImageUpload
  },
  methods: {
    tipToast (msg) {
      WeVue.Toast({
        duration: 2000,
        message: msg,
        type: 'text'
      });
    },
    imageuploaded(res) {
      if (res.Result === 1) {
        if (this.src.length < 5) {
          this.src.push(this.$store.state.passportUrl + res.Data.originalFile);
        } else {
          WeVue.Toast({
            duration: 2000,
            message: '最多只能上传5张图片！',
            type: 'text'
          });
        }
      }
    },
    delImg(index) {
      WeVue.Dialog.confirm(WeVue.Dialog.setDefaults({
        message: '确定要删除这张图片吗？',
        showCancelBtn: true,
        confirmText: '确认',
        cancelText: '取消'
        })).then(action => {
        this.src.splice(index, 1);
      });
    },
    saveMessage() {
      if (this.valueContent === '') {
        this.tipToast('请先输入您宝贵的意见！');
      } else {
        this.tipToast('意见反馈正在开发中！');
      }
    }
  }
};



