
import XImg from 'vux/src/components/x-img/index.vue'

import WeVue from 'we-vue';
export default {
props: {},
data() {
return {
converImage: '',
goodsClass: []
};
},
beforeRouteEnter (to, from, next) {
next(vm => {
vm.$store.commit('isLoading', true);
});
},
created() {
this.axios.get('static/data.json').then((res) => {
res = res.data;

this.converImage = res.goodsClass[0].converImage;
console.log(this.converImage);
}).catch(function (err) {
console.log(err);
});

this.axios({
method: 'get',
url: this.$store.state.apiUrl + '/app/Goods/GetGroupList?shopId=' + this.$store.state.shopId
}).then((res) => {
if (!res.data.IsError) {
res = res.data.Data;
this.goodsClass = res;
} else {
this.tipToast(res.data.Message);
}
}).catch((err) => {
console.log(err);
});
},
activated() {
this.$nextTick(() => {
this.$store.commit('isLoading', false);
});
},
methods: {
tipToast (msg) {
WeVue.Toast({
duration: 2000,
message: msg,
type: 'text'
});
}
},
computed: {



},
components: {
XImg
},
mounted() {}
};



