  import Bscroll from 'better-scroll';
  import orderList from '../../components/orderList/orderList.vue';
  import WeVue from 'we-vue';
  import {mapState} from 'vuex';
  export default {
    data () {
      return {
        GroupList: [],
        GroupListGoods: [],
        listHeight: [],
        scrollY: 0
      };
    },
    created() {
      WeVue.Indicator.open({
        text: 'loading',
        spinnerType: 'double-snake'
      });
      this.axios.get(this.$store.state.apiUrl + '/app/Goods/GetShopGroupGoods?shopId=' + this.$store.state.shopId).then((res) => {
        if (!res.data.IsError) {
          res = res.data.Data;
          this.GroupList = res;
          this.GroupList.sort((a, b) => {
            return b.GroupId - a.GroupId;
          });
          setTimeout(() => {
            WeVue.Indicator.close();
          }, 1000);
          this.$nextTick(() => {
//          this.$store.commit('isLoading', false);
            this._initScroll();
            this._calculateHeight();
          });
        } else {
          this.tipToast(res.data.Message);
        }
      }).catch(() => {
        WeVue.Indicator.close();
        this.tipToast('网络超时，请稍后再试！');
      });
    },
    mounted() {
    },
    activated() {
      setTimeout(() => {
        if (this.GroupList.length) {
          WeVue.Indicator.close();
//          this.$store.commit('isLoading', false);
        }
      }, 1000);
    },
    computed: mapState({
      cartCount: (data) => {
        let cartCount = 0;
        data.CartInfo.forEach((val) => {
          cartCount += val.count;
        });
        return cartCount;
      },
      orderIndex() {
        for (let i = 0; i < this.listHeight.length; i++) {
          let height1 = this.listHeight[i];
          let height2 = this.listHeight[i + 1];
          if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
            return i;
          }
        }
        return 0;
      }
    }),
    methods: {
      tipToast(msg) {
        WeVue.Toast({
          duration: 2000,
          message: msg,
          type: 'text'
        });
      },
      setGoodsInfo(GroupListGoodsItem) {
        this.$store.commit('goodsInfos', GroupListGoodsItem);
      },
      selectMenu(index, event) {
        if (!event._constructed) {
          return;
        }
        let foodList = this.$refs.foodsWrapper.getElementsByClassName('tabs');
        let el = foodList[index];
        this.foodsScroll.scrollToElement(el, 300);
      },
      _initScroll() {
        this.meunScroll = new Bscroll(this.$refs.menuWrapper, {
          click: true
        });

        this.foodsScroll = new Bscroll(this.$refs.foodsWrapper, {
          click: true,
          probeType: 3
        });

        this.foodsScroll.on('scroll', (pos) => {
          this.scrollY = Math.abs(Math.round(pos.y));
        });
      },
      _calculateHeight() {
        let foodList = this.$refs.foodsWrapper.getElementsByClassName('tabs');
        let height = 0;
        this.listHeight.push(height);
        for (let i = 0; i < foodList.length; i++) {
          let item = foodList[i];
          height += item.clientHeight;
          this.listHeight.push(height);
        }
      }
    },
    components: {
      orderList
    }
  };




