
import {mapState} from 'vuex';
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'


let els = null;
export default {
props: ['CartListInfos', 'CartListIndex'],
data() {
return {
isOpen: true,
isChecks: this.isCheck
};
},
filters: {},
mounted() {
let that = this;
document.addEventListener('touchstart', (ev) => {
if (that.isOpen) {
that.isOpen = false;
if (ev.target.className !== 'vux-swipeout-button') {
that.delHide();
}
setTimeout(() => {
that.isOpen = true;
}, 100);
}
}, false);
},
computed: mapState({}),
methods: {
changeNum(item, way) {
if (way > 0) {
item.count++;
item.OrderQuantity++;
} else {
item.count--;
item.OrderQuantity--;
if (way < 0) {
way = 0;
}
}
this.$store.commit('ModifyQuantity', item);
},
selectGoods(item) {
item.isChecked = !item.isChecked;
this.$store.commit('selectGoods', item);
},
filtersVal(item) {
if (Number(this.$refs.input.value) > 0) {
item.count = Number(this.$refs.input.value);
item.OrderQuantity = Number(this.$refs.input.value);
this.$store.commit('ModifyQuantity', item);
}
},
delHide(e) {
if (els !== null) {
els.close();
}
},
handleEvents(item) {
els = item;
},
swipeoutClose(ev) {
if (ev.target.className !== 'vux-swipeout-button') {
this.delHide();
}
},
goodsDel(type, item) {
if (type === 'delete') {
item.count = 0;
this.$store.commit('ModifyQuantity', item);
}
}
},
activated () {
this.delHide();
},
components: {
Swipeout,
SwipeoutItem,
SwipeoutButton
}
};



