
/* eslint-disable no-new */
/* eslint-disable no-undef */


import vueLoading from 'vue-loading-template';
import XImg from 'vux/src/components/x-img/index.vue'


import Swiper from 'swiper';
import 'swiper/dist/css/swiper.min.css';
import WeVue from 'we-vue';

import swiper from '../../components/swiper/swiper.vue';
import goodsList from '../../components/goodsList/goodsList.vue';

export default {
props: {},
data() {
return {
converImage: [],
goodsClass: [],
newGoods: [],
selectedGoods: [],
newGoodsList: [],
localCount: 'localCount',
nearbyAddress: [],
currentAddress: {},
seekFailed: '正在获取最近的配送点信息...',
isReposition: false,
isload: true
};
},
created() {
this.axios.get('static/data.json').then((res) => {
res = res.data;
this.converImage = res.converImage;


this.selectedGoods = res.selectedGoods;

this.$nextTick(() => {
new Swiper('.swiper-container-h', {
pagination: '.swiper-pagination',
slidesPerView: 1,
paginationClickable: true,
centeredSlides: true,
autoplay: 4000,
autoplayDisableOnInteraction: false
});
new Swiper('.swiper-container2', {
slidesPerView: 1.3,
spaceBetween: 10,
centeredSlides: true
});
new Swiper('.swiper-container3', {
slidesPerView: 4
});
this.$store.commit('isLoading', false);
});
}).catch(function (err) {
this.$store.commit('isLoading', false);
console.log(err);
});






this.axios({
method: 'get',
url: this.$store.state.apiUrl + '/app/Goods/GetGroupList?shopId=' + this.$store.state.shopId
}).then((res) => {
if (!res.data.IsError) {
res = res.data.Data;
this.goodsClass = res;
} else {
this.tipToast(res.data.Message);
}
}).catch((err) => {
console.log(err);
});
this.axios({
method: 'post',
url: this.$store.state.apiUrl + '/app/Goods/NewGoodsList?shopId=' + this.$store.state.shopId + '&topCount=3'
}).then((res) => {
if (!res.data.IsError) {
res = res.data.Data;
this.newGoods = res.slice(0, 3);
this.newGoodsList = res.slice(0, 10);
} else {
this.tipToast(res.data.Message);
}
}).catch((err) => {
console.log(err);
});
},
computed: {




},
mounted() {


this.loadJScript();
this.isload = false;


},
activated() {
this.$nextTick(() => {
this.$store.commit('isLoading', false);
this.$store.commit('isMenu', true);
});
},
methods: {
tipToast(msg) {
WeVue.Toast({
duration: 2000,
message: msg,
type: 'text'
});
},
setGoodsInfo(newGoodsItem) {
this.$store.commit('goodsInfos', newGoodsItem);
},
loadJScript() {
let that = this;
if (!window.AMap) {
clearTimeout(setTime);
var setTime = setTimeout(() => {
that.seekFailed = '定位超时';
that.isReposition = true;
}, 5000);
return;
} else {
clearTimeout(setTime);
}
let map, geolocation;

map = new AMap.Map('container', {
resizeEnable: true
});
map.plugin('AMap.Geolocation', function() {
geolocation = new AMap.Geolocation({
enableHighAccuracy: true,  // 是否使用高精度定位，默认:true
timeout: 500,          // 超过5秒后停止定位，默认：无穷大
buttonOffset: new AMap.Pixel(10, 20), //  定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
zoomToAccuracy: true,      //  定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
buttonPosition: 'RB'
});
map.addControl(geolocation);
geolocation.getCurrentPosition();
AMap.event.addListener(geolocation, 'complete', onComplete); // 返回定位信息
AMap.event.addListener(geolocation, 'error', onError);       // 返回定位出错信息
});


function onComplete(data) {
that.$store.commit('locationAddress', data);
AMap.service(['AMap.PlaceSearch'], function() {
var placeSearch = new AMap.PlaceSearch({  // 构造地点查询类
pageSize: 5,
type: '写字楼|餐饮|小区|学校',
pageIndex: 1,

map: map
});
var cpoint = [data.position.getLng(), data.position.getLat()];  // 中心点坐标
placeSearch.searchNearBy('', cpoint, 2000, function(status, result) {
if (result.info === 'OK') {
that.nearbyAddress = result.poiList.pois;
that.currentAddress = result.poiList.pois[0];
that.isReposition = false;
}
});
});
}

function onError(data) {
if (data.info === 'FAILED') {
that.seekFailed = '定位失败';
that.isReposition = true;
}
}
}
},
components: {
vueLoading,
swiper,
goodsList,
XImg
}
};



