/* eslint-disable no-new */
  import Swiper from 'swiper';
  import 'swiper/dist/css/swiper.min.css';
  import WeVue from 'we-vue';
  export default {
    props: {
      OrderDetail: {
        type: Array
      },
      isTitle: {
        type: String
      }
    },
    data () {
      return {
      };
    },
    filters: {
      OrderStatus: (val) => {
        let status = {
          '0': '已取消',
          '1': '已完成',
          '2': '待付款',
          '3': '正在配送',
          '4': '已审核'
        };
        return status[val];
      },
      formatMoney: (val) => {
        if (Number(val)) {
          return '¥ ' + Number(val).toFixed(2);
        } else {
          return val;
        }
      }
    },
    computed: {},
    mounted() {
    },
    methods: {
      tipToast(msg) {
        WeVue.Toast({
          duration: 2000,
          message: msg,
          type: 'text'
        });
      },
      cancelOrder(orderGuid) {
        WeVue.Indicator.open({
          text: 'loading',
          spinnerType: 'double-snake'
        });
        this.axios({
          method: 'post',
          url: this.$store.state.apiUrl + '/app/Order/CancelOrder?orderGuid=' + orderGuid
        }).then((res) => {
          WeVue.Indicator.close();
          if (!res.data.IsError) {
            this.tipToast('订单取消成功！');
            this.$router.replace('./my');
          } else {
            this.tipToast(res.data.Message);
          }
        }).catch(() => {
          WeVue.Indicator.close();
          this.tipToast('网络超时，请稍后再试！');
        });
      }
    },
    activated() {
//      if (this.OrderDetail.length) {
        setTimeout(() => {
          new Swiper('.swiper-container-order6', {
            slidesPerView: 5,
            spaceBetween: 10
          });
        }, 500);
      }
//    }
  };



